@media only screen and (max-width: 900px) {
  .About {
    width: 100%;
    height: 100vh;
    display: grid;
    align-items: flex-end;
    justify-items: center;
    scroll-snap-align: start;
    background: #161623;
    .Container {
      width: 100%;
      height: 88vh;
      display: grid;
      grid-template-rows: repeat(10, 1fr);
      grid-template-columns: repeat(10, 1fr);
      grid-template-areas:
        ". Title Title Title Title Title . . . ."
        ". Title Title Title Title Title . . . ."
        ". Illustration Illustration Illustration Illustration Illustration Illustration Illustration Illustration ."
        ". Illustration Illustration Illustration Illustration Illustration Illustration Illustration Illustration ."
        ". Illustration Illustration Illustration Illustration Illustration Illustration Illustration Illustration ."
        ". Illustration Illustration Illustration Illustration Illustration Illustration Illustration Illustration ."
        ". Illustration Illustration Illustration Illustration Illustration Illustration Illustration Illustration ."
        ". Description Description Description Description Description Description Description Description . "
        ". Description Description Description Description Description Description Description Description . "
        ". Download Download Download Download Download Download Download Download .";
    }
  }

  .Title {
    display: grid;
    grid-area: Title;
    place-items: center flex-start;

    p {
      margin: 0;
      color: white;
      font-size: 4.5vh;
      font-weight: 600;
      font-family: Montserrat;
    }

    .TitleBar {
      width: 12vw;
      height: 1vh;
      margin-top: 0.8vh;
      border-radius: 5px;
      background: #efa649;
    }
  }

  .Illustration {
    width: 100%;
    height: 90%;
    display: grid;
    place-items: center;
    grid-area: Illustration;

    .IllustrationContent {
      width: 80%;
      height: 80%;
    }
  }

  .Description {
    display: none;
  }

  .DescriptionMobile {
    display: grid;
    grid-area: Description;
    place-items: center flex-start;

    p {
      margin: 0;
      color: #bcbabc;
      font-size: 2.2vh;
      font-weight: 600;
      line-height: 150%;
      white-space: pre-wrap;
      font-family: Montserrat;
    }
  }

  .Download {
    display: grid;
    grid-area: Download;
    place-items: center flex-start;

    a {
      margin: 0;
      color: #efa649;
      font-size: 2.8vh;
      font-weight: 600;
      text-decoration: none;
      font-family: Montserrat;
    }
  }
}

@media only screen and (min-width: 900px) {
  .About {
    width: 100%;
    height: 100vh;
    display: grid;
    align-items: flex-end;
    justify-items: center;
    scroll-snap-align: start;
    background: #161623;

    .Container {
      width: 80%;
      height: 84%;
      display: grid;
      grid-template-rows: repeat(8, 1fr);
      grid-template-columns: repeat(8, 1fr);
      grid-template-areas:
        "Title Title . . . . . ."
        "Title Title . . . Illustration Illustration Illustration"
        "Description Description Description Description . Illustration Illustration Illustration"
        "Description Description Description Description . Illustration Illustration Illustration"
        "Description Description Description Description . Illustration Illustration Illustration"
        ". . . . . Illustration Illustration Illustration "
        "Download Download . . . Illustration Illustration Illustration "
        ". . . . . . . .";
    }

    .Title {
      display: grid;
      grid-area: Title;
      place-items: center flex-start;

      p {
        margin: 0;
        color: white;
        font-size: 4.5vh;
        font-weight: 600;
        font-family: Montserrat;
      }

      .TitleBar {
        width: 3vw;
        height: 1vh;
        margin-top: 0.8vh;
        border-radius: 5px;
        background: #efa649;
      }
    }

    .Description {
      display: grid;
      grid-area: Description;
      place-items: center flex-start;

      p {
        margin: 0;
        color: #bcbabc;
        font-size: 2.8vh;
        font-weight: 600;
        line-height: 150%;
        white-space: pre-wrap;
        font-family: Montserrat;
      }
    }

    .DescriptionMobile {
      display: none;
    }

    .Download {
      display: grid;
      grid-area: Download;
      place-items: center flex-start;

      a {
        margin: 0;
        color: #efa649;
        font-size: 2.8vh;
        font-weight: 600;
        text-decoration: none;
        font-family: Montserrat;
      }
    }

    .Illustration {
      display: grid;
      place-items: center flex-end;
      grid-area: Illustration;

      .IllustrationContent {
        height: 70%;
      }
    }
  }
}
