@media only screen and (max-width: 500px) {
  .Hero {
    width: 100%;
    height: 100vh;
    display: grid;
    place-items: end;
    background: #161623;
    scroll-snap-align: start;

    .Container {
      width: 100%;
      height: 88vh;
      display: grid;
      grid-template-rows: repeat(10, 1fr);
      grid-template-columns: repeat(10, 1fr);
      grid-template-areas:
        ". . . . . . . . . ."
        ". . . . . . . . . ."
        ". Content Content Content Content Content Content Content Content ."
        ". Content Content Content Content Content Content Content Content ."
        ". Content Content Content Content Content Content Content Content ."
        ". . . . . . . . . ."
        ". Footer Footer Footer Footer Footer Footer Footer Footer ."
        ". Footer Footer Footer Footer Footer Footer Footer Footer ."
        ". Footer Footer Footer Footer Footer Footer Footer Footer ."
        ". Footer Footer Footer Footer Footer Footer Footer Footer .";
    }

    .Content {
      display: grid;
      grid-area: Content;
      place-items: center flex-start;

      .Title {
        width: 100%;
        height: 100%;

        p {
          margin: 0;
          color: white;
          font-size: 8vh;
          font-weight: bold;
          font-family: GothamPro;
        }

        .TitleBar {
          display: none;
        }

        .TitleBarMobile {
          width: 18vw;
          height: 1.2vh;
          margin-top: 2vh;
          border-radius: 4px;
          background: #efa649;
        }
      }

      .Skills {
        display: none;
      }
    }

    .Footer {
      display: grid;
      grid-area: Footer;
      align-items: center;
      grid-template-rows: repeat(5, 1fr);
      grid-template-columns: repeat(5, 1fr);
      grid-template-areas:
        "Social . . . ."
        "Social . . . ."
        "Social . NavMessage NavMessage NavMessage"
        "Social . NavMessage NavMessage NavMessage"
        ". . . . .";

      .Social {
        height: 100%;
        display: flex;
        grid-area: Social;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;

        .SocialIcon {
          fill: white;
        }
      }

      .NavMessage {
        width: 100%;
        height: 100%;
        display: flex;
        color: #bcbabc;
        align-items: center;
        justify-content: flex-end;
        grid-area: NavMessage;

        .ScrollMessage {
          margin-right: 1vh;

          p {
            margin: 0;
            font-size: 2vh;
            white-space: nowrap;
          }
        }

        img {
          height: 5.5vh;
        }
      }
    }
  }
}

