@media only screen and (min-width: 900px) {
  .Work {
    width: 100%;
    height: 100vh;
    display: grid;
    align-items: flex-end;
    justify-items: center;
    scroll-snap-align: start;
    background: #161623;

    .Container {
      width: 80%;
      height: 84%;
      display: grid;
      grid-template-rows: repeat(8, 1fr);
      grid-template-columns: repeat(10, 1fr);
      grid-template-areas:
        "Title Title . . . . . . . ."
        "Title Title . . . . . . . ."
        "Details Details Details Images Images Images Images . . ."
        "Details Details Details Images Images Images Images . . ."
        "Details Details Details Images Images Images Images . . ."
        "Details Details Details Images Images Images Images . Navigation Navigation"
        "Details Details Details Images Images Images Images . Navigation Navigation"
        ". . . . . . . . Navigation Navigation";
    }

    .Title {
      display: grid;
      grid-area: Title;
      place-items: center flex-start;

      p {
        margin: 0;
        color: white;
        font-size: 4.5vh;
        font-weight: 600;
        font-family: Montserrat;
      }

      .TitleBar {
        width: 3vw;
        height: 1vh;
        margin-top: 0.8vh;
        border-radius: 5px;
        background: #efa649;
      }
    }

    .Details {
      width: 100%;
      height: 100%;
      display: flex;
      grid-area: Details;
      flex-direction: column;
      justify-content: space-evenly;

      .Name {
        margin: 0;
        color: #efa649;
        font-size: 4vh;
        font-weight: 600;
        font-family: Montserrat;
      }

      .Description {
        margin: 0;
        color: #bcbabc;
        font-size: 2.4vh;
        font-weight: 600;
        line-height: 150%;
        white-space: pre-wrap;
        font-family: Montserrat;
      }

      .Links {
        display: grid;
        grid-area: Links;
        place-items: center flex-start;

        .Link {
          display: flex;

          p {
            margin: 0;
            color: #bcbabc;
            font-size: 2.8vh;
            font-weight: 600;
            line-height: 150%;
            white-space: pre-wrap;
            font-family: Montserrat;
          }

          a {
            display: grid;
            place-items: center;
          }

          svg {
            height: 2.8vh;
            margin-left: 1vh;
          }
        }
      }

      .LinkMobile {
        display: none;
      }
    }

    .Images {
      display: grid;
      grid-area: Images;
      position: relative;
      place-items: center;

      .ImageBackground {
        width: 32vh;
        height: 32vh;
        border-radius: 50%;
        position: absolute;
        background: #efa649;
      }

      .Image {
        height: 50vh;
        position: absolute;
      }
    }

    .Navigation {
      display: grid;
      grid-area: Navigation;
      place-items: center;

      .NavIndicator {
        margin: 0;
        color: #bcbabc;
        font-size: 20vh;
        font-weight: 600;
        font-family: Montserrat;
      }

      .NavLinks {
        width: 58%;
        display: flex;
        margin-top: -30%;
        justify-content: space-between;

        .NavLink {
          margin: 0;
          font-size: 3vh;
          color: #efa649;
          font-weight: 600;
          font-family: Montserrat;
        }
      }
    }
  }
}
